.prod img {
    width: 500px;
    margin-top: 110px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    height: auto;
    block-size: auto;

    
}

.prod .descript {
    text-align: center;
    display: block;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 35px;
}

#light .prod {
    color: black;
}

#dark .prod {
    color: white;
}

.prod .about {
    display: block;
    align-items: center;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 15px;
    padding-left: 50px;
    padding-right: 50px;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: auto;
    width: fit-content;
    
}

#light .prod .about {
    color: black;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

#dark .prod .about {
    color: white;
    box-shadow: 0px 3px 15px black;
}

.addToCartBttn {
    background-color: transparent;
    border: 2px solid rgb(19, 19, 19);
    min-width: 100px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px; 
}

.addToCartBttn:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
}

.wishlist {
    padding-right: 20px;
     
}

#light .wishlist {
    color: grey;
}

#light .wishlist:hover {
    color: black;
}

#dark .wishlist {
    color: lightgrey;
}

#dark .wishlist:hover {
    color: grey;
}
